import i18n from "i18next";
import { initReactI18next } from 'react-i18next'
import translationEN from './Localisation/EN.json'
import translationRU from './Localisation/RU.json'

i18n
	.use(initReactI18next) // passes i18n down to react-i18next
	.init({
		resources: {
			ru: {
				translation: require('./Localisation/RU.json')
			},
			en: {
				translation: require('./Localisation/EN.json')
			},
		},
		lng: "ru",
		fallbackLng: "ru",
	});

export default i18n