import React, { useState } from 'react'
import { CSSTransition } from 'react-transition-group'

import { useTranslation } from 'react-i18next'
import i18next from '../I18next'

const PhoneMenu = ({ className }) => {
	
	const { t, i18n } = useTranslation()

	const changeLaguage = language => {
		i18next.changeLanguage(language)
	}

	const [isMenuShown, setIsMenuShown] = useState(false)

	return (
		<>
			<div onClick={() => setIsMenuShown(true)} className={`header__phone-menu-icon_${className}`}>
				<svg viewBox="0 0 384 384">
					<g>
						<rect x="0" y="277.333" width="384" height="42.667" />
						<rect x="0" y="170.667" width="384" height="42.667" />
						<rect x="0" y="64" width="384" height="42.667" />
					</g>
				</svg>
			</div>
			<CSSTransition classNames='css-transition-menu' timeout={500} unmountOnExit in={isMenuShown}>
				<div className='header__phone-menu'>
					<div onClick={() => setIsMenuShown(false)}>
						✖
					</div>
					<a href="#">{t("nav.calculator")}</a>
					<a href="#">{t("nav.services")}</a>
					<a href="#">{t("nav.our_news")}</a>
					<a href="#">{t("nav.reviews")}</a>
					<a href="#">{t("nav.consultation")}</a>
					<a href="#">{t("nav.advantages")}</a>
					<a href="#">{t("nav.production")}</a>
					<a href="#">{t("nav.question_answer")}</a>
					<a href="#">{t("nav.contacts")}</a>
				</div>
			</CSSTransition>
		</>
	)
}

export default PhoneMenu
