import React, { useState } from 'react'
import { CSSTransition } from 'react-transition-group'
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import '../Styles/Styles.css'
import { useTranslation } from 'react-i18next'
import i18next from '../I18next'

const Pictures = () => {

	const [isPictureShown, setisPictureShown] = useState(false)

	const { t, i18n } = useTranslation()

	const [isMapsShown, setIsMapsShown] = useState(false)

	const documents = [
		"https://p.calameoassets.com/120611080311-273ec90a2704cd09c6d0f10a5e814290/p1.jpg",
		'https://buckslib.org/wp-content/uploads/2016/07/document-scanner-station.jpg',
		'https://4ww1y37tl91gmoej12r01u1c-wpengine.netdna-ssl.com/wp-content/uploads/2019/08/TextDocument.png'
	]

	const [currentPicture, setCurrentPicture] = useState("https://p.calameoassets.com/120611080311-273ec90a2704cd09c6d0f10a5e814290/p1.jpg")

	return (
		<div className='info__pictures'>
			<p>{t('info.pictures')}</p>
			<div>
				{documents.map((value, index) => {
					return (
						<div style={{ width: '5.208vw', border: '0.26vw solid gray' }}>
							<img onClick={() => {
								setCurrentPicture(value)
								setisPictureShown(true)
							}} style={{ cursor: 'pointer', width: '100%' }} src={`${value}`} alt="Oops" />
						</div>
					)
				})}
			</div>
			<CSSTransition in={isPictureShown} unmountOnExit classNames='fullPicture'>
				<div>
					<TransformWrapper>
						<TransformComponent>
							<img src={currentPicture} alt="" />
						</TransformComponent>
					</TransformWrapper>
				</div>
			</CSSTransition>
			<CSSTransition className='close-button' in={isPictureShown} unmountOnExit style={{ color: 'white', cursor: 'pointer', position: "fixed", top: "20px", right: '20px', fontSize: '40px' }}>
				<div className='close-button' onClick={() => setisPictureShown(false)}>
					✖
				</div>
			</CSSTransition>
		</div>
	)
}

export default Pictures
