import React from 'react'
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"

const MyMapComponent = withScriptjs(withGoogleMap((props) =>
	<GoogleMap
		onDblClick={props.close}
		defaultZoom={8}
		defaultCenter={{ lat: 50.393839, lng: 30.491679 }}
		className='header__maps-itself'
	>
		{props.isMarkerShown && <Marker position={{ lat: 50.393839, lng: 30.491679 }} />}
	</GoogleMap>
))

export default MyMapComponent
